
div.home {
  width: 100vw;
  height: 100vh;
  padding: 30px;
  background-image: linear-gradient(45deg, #27AE60, #2ECC71);
}
div.home div.logo {
  width: 199px;
  height: 70px;
  background: url('../assets/cargill.png') no-repeat;
}
div.home div.text {
  top: 50%;
  left: 50%;
  position: absolute;
  text-align: center;
  color: #fff;
  transform: translateX(-50%) translateY(-50%);
}
div.home div.text h2 {
  font-size: 50px;
  font-weight: 900;
  margin-bottom: 20px;
}
div.home div.text p.subline {
  font-size: 22px;
  line-height: 32px;
  margin-bottom: 30px;
}
div.home div.text p.button a {
  font-size: 16px;
  text-transform: uppercase;
  background-color: #fff;
  display: inline-block;
  padding: 20px 40px;
  text-decoration: none;
  color: #2ECC71;
  border-radius: 60px;
  font-weight: 900;
  transition: box-shadow 1s;
  box-shadow: 0 10px 36px rgba(0,0,0,.1);
}
div.home div.text p.button a:hover {
  box-shadow: 0 16px 36px rgba(0,0,0,.2);
  transition: box-shadow 1s;
}
@media (max-width: 700px) {
div.home div.text {
    top: 0;
    left: 0;
    position: relative;
    margin-top: 50px;
    transform: translateX(0) translateY(0);
}
div.home div.text h2 {
    font-size: 30px;
}
div.home div.text p.subline {
    font-size: 18px;
    line-height: 30px;
    margin-bottom: 30px;
}
div.home div.text p.button a {
    font-size: 14px;
    padding: 15px 30px;
}
}



